.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.AppNavbar{
  margin: 0;
  padding: 0;
  height: 6rem;
}

.AppContent{
  min-height: calc(100dvh - 16rem);
}

#AppFooter{
  margin-top: 0;
  margin-bottom: -1rem;
  padding: 1rem 0;
  height: 10rem;

}
#MainPage{
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  height: 100%;
}
